body {
    overflow: hidden;
}
div#root {
    width: 100%;
    height: 100vh;
    max-height: 100%;
    display: flex;
    flex-flow: column;
}
h1 {
    font-family: "Charcoal";
}
h2 {
    font-family: "Charcoal";
    font-size: 1.25em;
}
h6 {
    font-family: "Charcoal";
    font-size: 0.875em;
    padding: 0;
    margin: 0;
}
p {
    font-family: "New York";
    font-size: 0.75em;
    padding: 0;
    margin: 0.75em 0;
}
li {
    font-family: "New York";
    font-size: 0.75em;
    padding: 0;
    margin: 0 0;
}
ul {
    padding-top: 0;
    padding-bottom: 0;
    margin: 0;
}
p.inline {
    margin: 0 0;
}